/**
 * 
 * @param {string} title title
 * @param {string} pageTitle title
 * @param {string} content 
 * @param {string} imageUrl url
 * @param {string} url url
 * @param {string} target id
 * @param {string} platform 
 * @param {string} callback 
 * @param {string} autojsxcx ,
 * @param {string} userName //
 * @param {number} type // 0 1 2
 * @param {string} path //path
 * @param {boolean} isLogin 
 * @param {boolean} ispc PCpcm
 * @param  error  {"1":"pc","0",""}
   @param  success  {"1":"","2":"APPAPP","3":"MPC"}
 */

import {
  isEscApp,
  isManApp,
  domAppendJs
} from "../util";
let Mixin_Share = {
  data: () => {
    return {
      mixin_share_isInWeiXinLite: false, //
      mixin_share_isInWeiXin: false, //
      mixin_share_params: {
        ispc: false, //pc
        isLogin: false //false 
      },
    }
  },
  mounted() {
    this.mixin_share_isMiniApp();
  },
  methods: {
    //wxjssdk
    mixin_share_isMiniApp() {
      domAppendJs("//res.wx.qq.com/open/js/jweixin-1.3.2.js", () => {
        this.mixin_share_isWeChatMiniApp();
      });
    },
    //
    mixin_share_isWeChatMiniApp() {
      const ua = window.navigator.userAgent.toLowerCase();
      return new Promise((resolve) => {
        //
        if (ua.indexOf("micromessenger") == -1) {
          this.mixin_share_isInWeiXinLite = false;
        } else {
          wx.miniProgram.getEnv((res) => {
            //
            if (res.miniprogram) {
              this.mixin_share_isInWeiXinLite = true;
            } else {
              //
              this.mixin_share_isInWeiXin = true;
              this.mixin_share_isInWeiXinLite = false;
            }
          });
        }
      });
    },
    //
    mixin_share_initShare: function () {
      if (window.fzshare || window.timer) {
        //window.fzshare 
        window.fzshare = null;
        window.timer = 0;
      }
      window.timer = setTimeout(function () {
        window.fzshare = new window.fz.share(this.mixin_share_params.options);
      }, 2000);
    },
    // 
    mixin_share_shareBtnClick() {
      return new Promise((res, rej) => {
        if (this.mixin_share_params.ispc) {
          rej({
            stauts: "error",
            code: "1"
          });
          return;
        }
        if (this.mixin_share_params.isLogin) {
          if (this.mixin_share_isInWeiXinLite || this.mixin_share_isInWeiXin) {
            res({
              stauts: "success",
              code: "1"
            })
          } else {
            //app
            if (isEscApp() || isManApp()) {
              window.fzshare.clickShare(); //callback
              res({
                stauts: "success",
                code: "2"
              })
            } else {
              res({
                stauts: "success",
                code: "3"
              })
            }
          }
        } else {
          rej({
            stauts: "error",
            code: "0"
          });
        }
      })
    }
  }
}

export {
  Mixin_Share
}



